@cor_fundo:  #842210;
@base: 		 #f04615;
@width:      0.5;
@the-border: 1px;
@base-color: #111;
@red:        #842210;

.teste1{
	background-color: saturate(@cor_fundo, 10%);
}

.teste2{
	background-color: saturate(@cor_fundo, 5%);
}

.class {
  width: percentage(@width); // returns `50%`
  color: saturate(@base, 5%);
  background-color: spin(lighten(@base, 25%), 8);
}

#header {
  color: @base-color * 3;
  border-left: @the-border;
  border-right: @the-border * 2;
}
#footer { 
  color: @base-color + #003300;
  border-color: desaturate(@red, 10%);
}